import Vue from 'vue'
import Router from 'vue-router'
import MyHello from '@/components/MyHello'
import HelloWorld from '@/components/HelloWorld'
import linkPage1 from '@/components/linkPage1'
import earthMap2 from '@/components/earthMap22'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'myHello',
      component: MyHello
    }, {
      path: '/HelloWorld',
      name: 'HelloWorldTest标题',
      component: HelloWorld
    }, {
      path: '/linkPage1',
      name: 'linkPage1',
      component: linkPage1
    }, {
      path: '/earthMap2',
      name: 'earthMap2',
      component: earthMap2
    }
  ]
})



